import React from "react";
import AdminButton from "../AdminButton/AdminButton";
import AdminInput from "../AdminInput/AdminInput";
import "./AdminSetting.scss";

function AdminSetting() {
  const colorValues = ["", "", ""];

  return (
    <div className="admin-setting">
      <div className="admin-setting__content">
        <h3 className="admin-setting__title"> </h3>
        <form className="admin-setting__form">
          <AdminInput
            label=" "
            id="carBrand"
            type="text"
            placeholder="  ..."
            position="left"
          />
          <AdminInput
            label=" "
            id="carType"
            type="text"
            placeholder="  ..."
          />
          <AdminInput
            label=" "
            id="carColors"
            type="text"
            placeholder="  ..."
            kind="top"
            position="left"
            addition="button"
            options={colorValues}
          />
        </form>
      </div>
      <div className="admin-setting__button-container">
        <div className="admin-setting__button-group">
          <AdminButton text="" type="button" position="left" />
          <AdminButton text="" type="button" disabled={true} />
        </div>
        <AdminButton text="" type="button" color="red" />
      </div>
    </div>
  );
}

export default AdminSetting;
